import Vue from 'vue'
import {Toast} from 'mint-ui'

function init(){
	var appid="wx01fca666dc60594f";
	var getSignUrl="http://www.you-best-link.com/index.php?m=Weixin&c=WechatApi&a=getJsSdkSign";
	getSignUrl=getSignUrl+"&appid="+appid;
	getSignUrl=getSignUrl+"&url="+encodeURIComponent(window.location.href);

	

	Vue.http.jsonp(getSignUrl).then(function(res){

		var sign=res.body;

		window.wx.config({
    		debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
    		appId: sign.appId, // 必填，公众号的唯一标识
    		timestamp: sign.timestamp, // 必填，生成签名的时间戳
    		nonceStr: sign.nonceStr, // 必填，生成签名的随机串
    		signature: sign.signature,// 必填，签名，见附录1
    		jsApiList: JSON.parse(sign.jsApiList) // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
		});

	});

}

export default init


