<template>
	<div>
		<img  class="warn_bg" :src="warn?onoff.warn_bg:'' " >
		<div class="status">
			<img src="../assets/status_bg.gif">
			<p class="tem" >27℃</p>
			<p class="target_tem" >目标：{{target_tem}}℃</p>
			<p class="work_status">工作中</p>
		</div>
		<div class="wrap">
			<div class="wrap-item" @click="togglewarm()">
				<img :src="warm?onoff.warm_on:onoff.warm_off">
	          	<p>温调</p>
          	</div>
          	<div class="wrap-item" @click="togglehot()">
				<img :src="hot?onoff.hot_on:onoff.hot_off">
	          	<p>热调</p>
          	</div>
          	<div class="wrap-item" @click="togglerice()">
				<img :src="rice?onoff.rice_on:onoff.rice_off">
	          	<p>米糊</p>
          	</div>
          	<div class="wrap-item" @click="togglecoffee()">
				<img :src="coffee?onoff.coffee_on:onoff.coffee_off">
	          	<p>咖啡</p>
          	</div>
		</div>
		<div class="wrap">
			<div class="wrap-item" @click="toggletea()">
				<img :src="tea?onoff.tea_on:onoff.tea_off">
	          	<p>泡茶</p>
          	</div>
          	<div class="wrap-item" @click="toggleboil()">
				<img :src="boil?onoff.boil_on:onoff.boil_off">
	          	<p>沸腾</p>
          	</div>
          	<div class="wrap-item" @click="toggledechlorinate()">
				<img :src="dechlorinate?onoff.dechlorinate_on:onoff.dechlorinate_off">
	          	<p>除氯</p>
          	</div>
          	<div class="wrap-item" @click="toggletemset()" @click.stop="showPicker2">
				<img :src="temset?onoff.temset_on:onoff.temset_off">
	          	<p>温度设置</p>
          	</div>
		</div>

		<div class="timing_off">
			<div class="off_type">
				<span>定时关机</span>
				<div class="btn">
					<label @click="toggletiming_off()">
						<span>单次</span>
						<img :src="off_type?onoff.timing_once:onoff.timing_circulation">
					</label>
					<label @click="toggletiming_off()">
						<span>循环</span>
						<img :src="off_type?onoff.timing_circulation:onoff.timing_once">
					</label>
				</div>
			</div>
			<div class="off_time">
				<span>关机时间</span>
				<label @click.stop="showPicker">{{timeShow}}&nbsp&nbsp&nbsp></label>
			</div>
		</div>

		<div class="timing_off">
			<div class="off_type">
				<span>定时开机</span>
				<div class="btn">
					<label @click="toggletiming_on()">
						<span>单次</span>
						<img :src="on_type?onoff.timing_once:onoff.timing_circulation">
					</label>
					<label @click="toggletiming_on()">
						<span>循环</span>
						<img :src="on_type?onoff.timing_circulation:onoff.timing_once">
					</label>
				</div>
			</div>
			<div class="off_time">
				<span>开机时间</span>
				<label @click.stop="showPicker1">{{timeShow1}}&nbsp&nbsp&nbsp></label>
			</div>
		</div>

		<div class="picker-bg" v-show="timePickerShow" @click.stop="hidePicker">
		    <div class="picker-container" @click.stop="">
		        <div class="picker-title">请选择关机时间</div>
		        <mt-picker :slots="timeSlots" @change="onTimeValueChange"></mt-picker>
		        <div class="picker-btn" @click="timePickerOk">确定</div>
		    </div>
	    </div>
	    <div class="picker-bg" v-show="timePickerShow1" @click.stop="hidePicker1">
		    <div class="picker-container" @click.stop="">
		        <div class="picker-title">请选择开机时间</div>
		        <mt-picker :slots="timeSlots" @change="onTimeValueChange1"></mt-picker>
		        <div class="picker-btn" @click="timePickerOk1">确定</div>
		    </div>
	    </div>
	    <div class="picker-bg" v-show="temPickerShow" @click.stop="hidePicker2">
		    <div class="picker-container" @click.stop="">
		        <div class="picker-title">请选择目标温度</div>
		        <mt-picker :slots="temSlots" @change="onTemValueChange"></mt-picker>
		        <div class="picker-btn" @click="temPickerOk">确定</div>
		    </div>
	    </div>
		
	</div>
</template>

<script>
	export default{
		data:function(){
			return {
				onoff: {
					warn_bg:  require("../assets/warn_bg.png"),
	                warm_on:  require("../assets/warm_on.png"),
	                warm_off: require("../assets/warm_off.png"),
	                hot_on: require("../assets/hot_on.png"),
	                hot_off: require("../assets/hot_off.png"),
	                rice_on: require("../assets/rice_on.png"),
	                rice_off: require("../assets/rice_off.png"),
	                coffee_on: require("../assets/coffee_on.png"),
	                coffee_off: require("../assets/coffee_off.png"),
	                tea_on: require("../assets/tea_on.png"),
	                tea_off: require("../assets/tea_off.png"),
	                boil_on: require("../assets/boil_on.png"),
	                boil_off: require("../assets/boil_off.png"),
	                dechlorinate_on: require("../assets/dechlorinate_on.png"),
	                dechlorinate_off: require("../assets/dechlorinate_off.png"),
	                temset_on: require("../assets/temset_on.png"),
	                temset_off: require("../assets/temset_off.png"),
	                timing_once: require("../assets/timing_once.png"),
	                timing_circulation: require("../assets/timing_circulation.png")
	            },
	            warn: true,
	            target_tem: 51,
	         	warm: false,
	         	hot: false,
	         	rice: false,
	         	coffee: false,
	         	tea: false,
	         	boil: false,
	         	dechlorinate: false,
	         	temset: false,
	         	off_type: true,
	         	off_time: 100,
	            timePickerShow: false,
	            timePicker: "",
	         	on_type: false,
	         	on_time: 200,
	            timePickerShow1: false,
	            timePicker1: "",
	            temPickerShow: false,
	            temPicker: ""
			}
		},
		computed: {
			timeSlots: function () {
                return [
	                {
	                  flex: 1,
	                  values: this.createDataArray(0, 23, 1),
	                }, {
	                  flex: 1,
	                  divider: true,
	                  content: '小时',
	                }, {
	                  flex: 1,
	                  values: this.createDataArray(0, 59, 1),
	                }, {
	                  flex: 1,
	                  divider: true,
	                  content: '分钟',
	                }
                ];
            },
            temSlots: function () {
                return [
	                {
	                  flex: 2,
	                  values: this.createDataArray(0, 100, 1),
	                }, {
	                  flex: 1,
	                  divider: true,
	                  content: '摄氏度',
	                }
                ];
            },
	        timeShow: function(){
		        var starttime =this.off_time;
		        var hShow= parseInt(starttime/60)<10?'0'+parseInt(starttime/60):parseInt(starttime/60);
		        var mShow= starttime%60<10?'0'+starttime%60:starttime%60;
		        return hShow+' : '+mShow;
	        },
	        timeShow1: function(){
		        var starttime =this.on_time;
		        var hShow= parseInt(starttime/60)<10?'0'+parseInt(starttime/60):parseInt(starttime/60);
		        var mShow= starttime%60<10?'0'+starttime%60:starttime%60;
		        return hShow+' : '+mShow;
	        }
		},
		created:function(){

		},
		methods:{
			togglewarm: function(){
				this.warm=!this.warm;
			},
			togglehot: function(){
				this.hot=!this.hot;
			},
			togglerice: function(){
				this.rice=!this.rice;
			},
			togglecoffee: function(){
				this.coffee=!this.coffee;
			},
			toggletea: function(){
				this.tea=!this.tea;
			},
			toggleboil: function(){
				this.boil=!this.boil;
			},
			toggledechlorinate: function(){
				this.dechlorinate=!this.dechlorinate;
			},
			toggletemset: function(){
				this.temset=!this.temset;
			},
			toggletiming_off: function(){
				this.off_type=!this.off_type;
			},
	        createDataArray: function (from, to, divider) {
	            var dataArray = [];
		        for (var i = from; i <= to; i += divider) {
		            dataArray.push(i);
		        }
		        return dataArray;
	        },
	        onTimeValueChange(picker, values) {
	            this.timePicker = picker;
	        },
	        showPicker: function () {
	            this.timePickerShow = true;
	        },
	        hidePicker: function () {
	            this.timePickerShow = false;
	        },
	        timePickerOk: function () {
	            this.hidePicker();
	            if (this.timePicker) {
	                var h=this.timePicker.getValues()[0];
	                var m=this.timePicker.getValues()[1];
	                if(h==undefined){
	                    h=0;
	                }
	                // this.setDataPoint(5,h* 60 + m);
	                this.off_time=h* 60 + m;
	            }
	        },
	        toggletiming_on: function(){
				this.on_type=!this.on_type;
			},
	        onTimeValueChange1(picker, values) {
	            this.timePicker1 = picker;
	        },
	        showPicker1: function () {
	            this.timePickerShow1 = true;
	        },
	        hidePicker1: function () {
	            this.timePickerShow1 = false;
	        },
	        timePickerOk1: function () {
	            this.hidePicker1();
	            if (this.timePicker1) {
	                var h=this.timePicker1.getValues()[0];
	                var m=this.timePicker1.getValues()[1];
	                if(h==undefined){
	                    h=0;
	                }
	                // this.setDataPoint(5,h* 60 + m);
	                this.on_time=h* 60 + m;
	            }
	        },
	        onTemValueChange(picker, values) {
	            this.temPicker = picker;
	        },
	        showPicker2: function () {
	            this.temPickerShow = true;
	        },
	        hidePicker2: function () {
	            this.temPickerShow = false;
	        },
	        temPickerOk: function () {
	            this.hidePicker2();
	            if (this.temPicker) {
	                var t=this.temPicker.getValues()[0];
	                if(t==undefined){
	                    t=0;
	                }
	                this.target_tem=t;
	            }
	        }
		}
	}
</script>

<style scoped>
	.warn_bg{
		width: 13%;
		left: 5%;
		top: 10%;
		position: absolute;
	}
	.status{
		width: 100%;
		height: 100%;
		position: relative;
		margin-bottom: 20px;
	}
	.status img{
		width: 30%;
		height: 30%;
		margin-left: 35%;
		margin-top: 15%;
	}
	.status p{
		position: absolute;
		font-weight: bold;
	}
	.status .tem{
		top:42%;
		left: 40%;
		right:40%;
		text-align: center;
		color: #3881b8;
		font-size: 24px;
	}
	.status .target_tem{
		top:60%;
		left: 40%;
		right:40%;
		text-align: center;
		color: #898989;
		font-size: 12px;
	}
	.status .work_status{
		top:68%;
		left: 40%;
		right:40%;
		text-align: center;
		color: #3881b8;
		font-size: 12px;
	}

	.wrap{
		display: flex;
	}
	.wrap .wrap-item {
	    flex: 1;
	    text-align: center;
	    padding-top: 5px;
		border-right: 2px solid #efefef;
		border-bottom: 2px solid #efefef;

    }
    .wrap .wrap-item img{
    	width: 50px;
    	height: 50px;
    }
	
	.timing_off {
		margin-bottom: 30px;
	}
    .timing_off .off_type,.off_time{
    	position: relative;
    	height: 50px;
    	width: 100%;
    	border-bottom: 1px solid #efefef;
    	line-height: 50px;
    }
    .timing_off .off_type img{
    	height: 50px;
    }
    .timing_off .off_type,.off_time span{
		margin-left: 20px;
    }
    .timing_off .off_type .btn{
    	float: right;
    }
    .timing_off .off_type label{
    	float: left;
    	margin-right: 40px;
    }
    .timing_off .off_type label span{
    	margin-left: 0px;
    	vertical-align: top;
    }

	.timing_off .off_time label {
		position: absolute;
		right: 28px;
	}


    .picker-bg {
	    width: 100%;
	    height: 100%;
	    background: rgba(0, 0, 0, 0.2);
	    position: fixed;
	    top: 0;
	    z-index: 88;
	}
	.picker-container {
	    width: 80%;
	    margin: 80px 10%;
	    background: rgba(255, 255, 255, 0.95);
	    border-radius: 5px;
	    box-shadow: 0 0 5px #666;
	}
	.picker-title {
	    text-align: center;
	    padding: 15px;
	    color: #111;
	    border-bottom: 1px solid #ddd;
	}
	.picker-btn {
	    padding: 15px;
	    text-align: center;
	    border-top: 1px solid #ddd;
	    color: #9999ff;
	}
</style>


